.modal {
  position: relative;
  background: #cc457d;
  border-radius: 10px;
  box-sizing: 0 3px 15px rgba(0, 0, 0, 0.5);
  display: grid;
  grid-auto-columns: 1fr;
  justify-content: center;
  align-items: center;
  font-family: "Helvetica", "Segoe UI Light", sans-serif;
  font-weight: lighter;
  text-align: center;
  list-style: none;
  margin: 10vh;
  margin-left: 15vw;
  margin-right: 15vw;
  padding-top: 2vh;
  padding-bottom: 2vh;
  padding-left: 2vw;
  padding-right: 2vw;
}
.modal ul li {
  list-style: none;
  padding: 10px;
  font-size: 20px;
}

.title {
  text-align: center;
}

.contact {
  display: inline-flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin: 1rem;
}

.contact a:active, .contact a:visited, .contact a:link {
  color: hsl(224, 43%, 90%);
  text-decoration: none;
}

.contact a:hover {
  cursor: pointer;
}
* {
  box-sizing: border-box;
}

/* basic stylings ------------------------------------------ */
body #signup-login {
  display: grid;
  align-items: center;
  justify-content: space-around;
}

.container #signup-login {
  width: 600px;
  margin: 30px auto 0;
  display: block;
  background: hsl(224, 43%, 90%);
  padding: 10px 50px 50px;
}

#signup-login h3 {
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

/* form starting stylings ------------------------------- */
#signup-login .group {
  position: relative;
  margin-bottom: 45px;
}

#signup-login input {
  font-size: 18px;
  padding: 10px 10px 10px 5px;
  display: block;
  width: 300px;
  border: none;
  border-bottom: 1px solid #555;
  border-radius: 5px;
}

#signup-login input:focus {
  outline: none;
}

/* LABEL ======================================= */
#signup-login label {
  color: #555;
  font-size: 18px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 5px;
  top: 10px;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

/* active state */
#signup-login input:focus ~ label,
#signup-login input:valid ~ label {
  top: -20px;
  font-size: 14px;
  color: hsl(224, 43%, 25%);
}

/* BOTTOM BARS ================================= */
#signup-login .bar {
  position: relative;
  display: block;
  width: 300px;
}

#signup-login .bar:before,
#signup-login .bar:after {
  content: "";
  height: 2px;
  width: 0;
  bottom: 1px;
  position: absolute;
  background: hsl(224, 43%, 25%);
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}
#signup-login .bar:before {
  left: 50%;
}
#signup-login .bar:after {
  right: 50%;
}

/* active state */
input:focus ~ .bar:before,
input:focus ~ .bar:after {
  width: 50%;
}

/* HIGHLIGHTER ================================== */
.highlight {
  position: absolute;
  height: 60%;
  width: 100px;
  top: 25%;
  left: 0;
  pointer-events: none;
  opacity: 0.5;
}

/* active state */
input:focus ~ .highlight {
  -webkit-animation: inputHighlighter 0.3s ease;
  animation: inputHighlighter 0.3s ease;
}

/* ANIMATIONS ================ */
@-webkit-keyframes inputHighlighter {
  from {
    background: hsl(224, 43%, 25%);
  }
  to {
    width: 0;
    background: transparent;
  }
}
@keyframes inputHighlighter {
  from {
    background: hsl(224, 43%, 25%);
  }
  to {
    width: 0;
    background: transparent;
  }
}

