  grid-template-areas:
    "title ."
    "tabs button";
  grid-column-gap: 1rem;
  width: 95vw;
  padding: 5vh;
}

.tabWidget h2 {
  grid-area: title;
}

.categoryTabs {
  padding-bottom: 1%;
}

.categoryTabs input[type="radio"] {
  opacity: 0;
  width: 0;
}

.categoryTabs label {
  background-color: hsl(224, 43%, 25%);
  padding: 10px 20px;
  font-size: 2.8vmin;
  font-family: "Nunito Sans", sans-serif;
  color: hsl(224, 43%, 90%);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 10px 10px 0 0;
}

.categoryTabs label:hover {
  border: 1px solid hsl(224, 43%, 90%);
}

.categoryTabs input[type="radio"]:checked + label {
  background-color: #cc457d;
  border-color: #ff958c;
}

#expandButton {
  height: 3rem;
  width: 3rem;
  border: none;
  background: url(/img/expand.png) no-repeat;
  background-size: 100% 50%;
  background-position: center;
  margin-left: 1rem;
  cursor: pointer;
  outline: none;
  grid-area: button;
  align-self: start;
}

#expandButton.expanded {
  background: url(/img/collapse.png) no-repeat;
  background-size: 100% 50%;
  background-position: center;
}

#recentTabs {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: flex-start;
  margin: auto;
  padding-top: 1vh;
  height: 8vh;
  border-radius: 10px;
  overflow-y: hidden;
  -webkit-transition: height 0.5s;
  transition: height 0.5s;
  grid-area: tabs;
  width: 100%;
}

#recentTabs.expanded {
  overflow-y: scroll;
  height: 60vh;
  -webkit-transition: height 0.5s;
  transition: height 0.5s;
}

#recentTabs > a.recentTab {
  width: 26%;
  height: 5vh;
  border-radius: 10px;
  margin-bottom: 2vh;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  cursor: pointer;
  -webkit-transition: border 0.2s;
  transition: border 0.2s;
  text-decoration: none;
  color: hsl(224, 43%, 25%);
  background-color: hsl(224, 43%, 96%);
  padding: 0.5rem 0.5rem 0rem 0.5rem;
  box-shadow: 0.25rem 0.25rem 0.25rem rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(0, 0, 0, 0);
}

#recentTabs > a.recentTab:hover {
  -webkit-transition: border 0.2s;
  transition: border 0.2s;
  box-shadow: 0.25rem 0.25rem 0.25rem rgba(0, 0, 0, 0.4);
  border: 1px solid hsl(224, 43%, 25%);
}

#weather {
  margin-top: 0;
