}

/* width */
::-webkit-scrollbar {
  width: 15px;
  border-radius: 15px;
  padding-right: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: hsl(224, 43%, 95%);
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: hsl(224, 43%, 25%);
  border-radius: 15px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

